.App {
}
.App.scrolling .App-header {
  -webkit-transition: background-color 0.8s;
  -o-transition: background-color 0.8s;
  transition: background-color 0.8s;
  background-color: rgba(0, 0, 0, 1);
}
.App-header {
  background-color: rgba(0, 0, 0, 0);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100px;
  color: white;
  z-index: 999;
}
.bars {
  position: absolute;
  top: 33px;
  left: 40px;
  width: 36px;
  height: 22px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  z-index: 998;
  opacity: 0;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
}
.bars > div {
  width: 8px;
  height: 100%;
  background-color: #ffffff;
}
.dynamic-nav {
  position: absolute;
  top: 28px;
  left: 94px;

  height: auto;
  z-index: 998;
}
.dynamic-nav .dn-item {
  font-size: 29px;
  color: white;
  font-weight: 700;
  display: inline-block;
  padding: 0px;
  overflow: hidden;
  width: 0px;
  -webkit-transition: width 1.8s cubic-bezier(0.165, 0.84, 0.44, 1), opacity 1.8s;
  -o-transition: width 1.8s cubic-bezier(0.165, 0.84, 0.44, 1), opacity 1.8s;
  transition: width 1.8s cubic-bezier(0.165, 0.84, 0.44, 1), opacity 1.8s;
  text-align: center;
  opacity: 0;
}
.dynamic-nav .dn-item span {
  width: 100%;
}
.dynamic-nav .dn-item:hover span {
}
.open-design .dynamic-nav .dn-item.dn-projects,
.open-people .dynamic-nav .dn-item.dn-projects {
  width: 130px;
  opacity: 1;
}
.open-design .dynamic-nav .dn-item.dn-people,
.open-projects .dynamic-nav .dn-item.dn-people {
  width: 120px;
  opacity: 1;
}
.open-projects .dynamic-nav .dn-item.dn-design,
.open-people .dynamic-nav .dn-item.dn-design {
  width: 100px;
  opacity: 1;
}

.open-design .dynamic-nav .dn-item span,
.open-projects .dynamic-nav .dn-item span,
.open-people .dynamic-nav .dn-item span {
  width: 120px;
}
.header-logo {
  position: absolute;
  right: 40px;
  top: 20px;
  height: 40px;
  width: 80px;
  z-index: 990;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
  opacity: 0;
}

.footer-logo {
  position: absolute;
  bottom: 20px;
  right: 40px;
  height: 60px;
  z-index: 990;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
  opacity: 1;
}

.open .footer-logo {
  opacity: 0;
}
.open .header-logo {
  opacity: 1;
}
.open .bars {
  opacity: 1;
}
.header-logo svg {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.st0 {
  fill: #ffffff;
}
.st1 {
  fill: #ff595a;
}

.panels {
  width: 100vw;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
}
.panels.mobile {
  -ms-flex-direction: column;
      flex-direction: column;
}
.panels.mobile .panel {
  width: 100vw;
  height: 33.3333333vh;
}
.panels.mobile .panel .message {
  display: none;
}
.panel {
  width: 33.33333333vw;
  color: #ffffff;

  -webkit-transition: width 1.4s, height 1.4s cubic-bezier(0.165, 0.84, 0.44, 1);

  -o-transition: width 1.4s, height 1.4s cubic-bezier(0.165, 0.84, 0.44, 1);

  transition: width 1.4s, height 1.4s cubic-bezier(0.165, 0.84, 0.44, 1);
  position: relative;
  overflow: hidden;
}
.panel .content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  pointer-events: none;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
  opacity: 1;
}
.panel .message {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
  background-position: center;
  background-size: center;
  background-repeat: no-repeat;
  opacity: 0;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
}
.open-design .panel.design .content h2,
.open-projects .panel.projects .content h2,
.open-people .panel.people .content h2 {
  top: 90px;
}

.open-design .panel.design .message,
.open-projects .panel.projects .message,
.open-people .panel.people .message {
  opacity: 1;
}
.panel .content h2 {
  position: absolute;
  z-index: 9999;
  font-size: 48px;
  top: 50%;
  left: 20px;
  -webkit-transition: top 1.8s cubic-bezier(0.165, 0.84, 0.44, 1), background-color 0.8s;
  -o-transition: top 1.8s cubic-bezier(0.165, 0.84, 0.44, 1), background-color 0.8s;
  transition: top 1.8s cubic-bezier(0.165, 0.84, 0.44, 1), background-color 0.8s;
  margin-top: -30px;
  background-color: rgba(0, 0, 0, 0);
  padding: 20px;
}

.App.scrolling.open-design .panels .panel.design .content h2,
.App.scrolling.open-projects .panels .panel.projects .content h2,
.App.scrolling.open-people .panels .panel.people .content h2 {
  position: fixed;
  z-index: 999;
  background-color: rgba(0, 0, 0, 1);
}

.panels.mobile .panel .content h2 {
  top: 70px;
  font-size: 38px;
}
.open-design .panels.mobile .panel.design .content h2,
.open-projects .panels.mobile .panel.projects .content h2,
.open-people .panels.mobile .panel.people .content h2 {
  top: 110px;
  font-size: 38px;
}

.scrolling .open-design .panels.mobile .panel.design .content h2 {
  position: fixed;
}
.panel .img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background-position: center;
  background-size: cover;
  opacity: 0;
  -webkit-transition: opacity 0.8s;
  -o-transition: opacity 0.8s;
  transition: opacity 0.8s;
}

.panels.showdesign .panel.design,
.panels.showprojects .panel.projects,
.panels.showpeople .panel.people {
  width: 100vw;
}
.panels.showdesign .panel.projects,
.panels.showdesign .panel.people,
.panels.showpeople .panel.design,
.panels.showpeople .panel.projects,
.panels.showprojects .panel.design,
.panels.showprojects .panel.people {
  width: 30vw;
}
.panels.showdesign .panel.projects .content,
.panels.showdesign .panel.people .content,
.panels.showprojects .panel.design .content,
.panels.showprojects .panel.people .content,
.panels.showpeople .panel.design .content,
.panels.showpeople .panel.projects .content {
  opacity: 0.5;
}
/* .panel.show{
  width:100vw;
} */
.panel.show .img {
  opacity: 0.5;
}

.open-design .panel.projects,
.open-design .panel.people,
.open-projects .panel.design,
.open-projects .panel.people,
.open-people .panel.design,
.open-people .panel.projects {
  width: 0vw !important;
}
.open-design .panel.design,
.open-projects .panel.projects,
.open-people .panel.people {
  width: 100vw;
}

.open-design .panels.mobile .panel.design,
.open-projects .panels.mobile .panel.projects,
.open-people .panels.mobile .panel.people {
  height: 100vh !important;
  width: 100vw;
}
.open-design .panels.mobile .panel.projects,
.open-design .panels.mobile .panel.people,
.open-projects .panels.mobile .panel.design,
.open-projects .panels.mobile .panel.people,
.open-people .panels.mobile .panel.design,
.open-people .panels.mobile .panel.projects {
  height: 0vh;
  width: 100vw !important;
}
.open-people .panel.people .img,
.open-projects .panel.projects .img,
.open-design .panel.design .img {
  opacity: 1;
}

.panel.design {
  background-color: #2e2e2e;
}
.panel.projects {
  background-color: #393939;
}
.panel.people {
  background-color: #434343;
}

.main {
  position: absolute;
  top: 100vh;
  left: 0;
  right: 0;
  overflow: hidden;
  height: 0vh;
}
.main-wrapper {
  padding: 40px;
}
.open .main {
  height: 100vh;
}

.row {
  display: -ms-flexbox;
  display: flex;
  margin: 0 -20px;
}
.row .col {
  background-color: #ccc;
  min-height: 300px;
  margin: 20px;
  width: 100%;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



